import React from 'react';
import modal from './ModalPopup'


function OpenModal(title) {
    document.getElementById(title).showModal();
}

const CardTemplate = (img, title, body, btnType) => {
    return (
        <div className="card card-compact max-w-36 h-full border hover:shadow-md border-mid-green hover:shadow-mid-green image-full" onClick={() => OpenModal(title)}>
            <figure><img src={img} alt={title} /></figure>
            <div className="card-body">
            </div>
            {modal(img, title, body, btnType)}
        </div>
    );
}

export default CardTemplate;