import React from 'react';

function HandleBtnClick(btnType) {

    var url = "https://antelope-games.itch.io/";

    switch(btnType) {
        case 'rePair':
            url = "https://renderquake.itch.io/re-pair"; break;
        case 'prakruti':
            url = "https://renderquake.itch.io/prakruthi"; break;
        case 'markerMan':
            url = "https://antelope-games.itch.io/marker-man"; break;
        case 'keepDist':
            url = "https://antelope-games.itch.io/keep-distance"; break;
        default:
            url = "https://antelope-games.itch.io/";
    }

    var win = window.open(url, '_blank');
    win.focus();
        
}

const Modal = (img, title, body, btnType) => {
    return(
        <div>
            {/* <button className="btn" onClick={()=>document.getElementById('my_modal_3').showModal()}>open modal</button> */}
            <dialog id={title} className="modal">
                <div className="modal-box w-11/12 max-w-5xl bg-dark-green">
                    <form method="dialog">
                    <button className="btn btn-sm btn-circle btn-ghost absolute right-2 top-2 text-terminal-green">X</button>
                    </form>
                    <div className="hero h-auto">
                        <div className="hero-content flex-col lg:flex-row-reverse sm:flex-col">
                            <img src={img} className="sm:max-w-24 md:max-w-48 lg:max-w-96 rounded-lg shadow-2xl" alt={title}/>
                            <div>
                                <h1 className="text-5xl font-bold">{title}</h1>
                                <p className="py-6">{body}</p>
                                <button className='btn px-4 text-4xl rounded bg-terminal-green font-pixelify' onClick={() => HandleBtnClick(btnType)}>itch.io</button>
                            </div>
                        </div>
                    </div>
                </div>
            </dialog>
        </div>
    );
}

export default Modal;